<template>
    <div   id="comdetail">
         <el-row :gutter="10">
         <el-col :xs="24" :sm="24" :md="24" ><div class="grid-content top">
           <span></span>    
            </div></el-col>
        </el-row>
        <div class="contain">
         <el-row>
            
          <v-input title="" :value="data.cname"></v-input>
          <v-input title="" :value="data.companyCode"></v-input>
          <v-input title="" :value="data.companyType"></v-input>
          <v-input title="" :value="data.register"></v-input>
          <v-input title="" :value="data.caddress"></v-input>
          <v-input title="" :value="data.contway"></v-input>
          <v-input title="" :value="data.fax"></v-input>
          <v-input title="" :value="data.email"></v-input>
          <v-input title="" :value="data.postcode"></v-input>
          <v-input title="" :value="data.openDate"></v-input>
          <v-input title="" :value="data.ecotype"></v-input>
          <v-input title="" :value="data.legPersonCode"></v-input>
          <v-input title="" :value="data.legPerson"></v-input>
          <v-input title="" :value="data.legPersonIdcode"></v-input>
          <v-input title="" :value="data.defPrincipal"></v-input>
          <v-input title="" :value="data.defDepartContway"></v-input>
          <v-input title="" :value="data.businessScope"></v-input>
          <v-input title=""  unit='' :value="data.registeredCapital"></v-input>
          <v-input title=""  unit='' :value="data.floorSpace"></v-input>
          <v-input title="" :value="data.agency"></v-input>
          <v-input title="" :value="data.agencyName"></v-input>
          <v-input title="" :value="data.specRecordCode"></v-input>
          <v-input title="" :value="data.specRecordAgency"></v-input>
          <v-input title="" :value="data.specRecordAname"></v-input>
          <v-input title="" :value="data.induLicenceCode"></v-input>
          <v-input title="" :value="data.induLicenceAgency"></v-input>
          <v-input title="" :value="data.unisocialCreditCode"></v-input>
          <v-input title="" :value="data.unisocialLicenceAgency"></v-input>
          <v-input title="" :value="data.busLicenceCode"></v-input>
          <v-input title="" :value="data.busLicenceAgency"></v-input>
          <v-input title="" :value="data.taxLicenceCode"></v-input>
          <v-input title="" :value="data.taxLicenceAgency"></v-input>
          





         </el-row>
         </div>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    data() {
        return {
            data:"",

        }
    },
    mounted() {
         this.getdata()
    },
    methods: {
        getdata(){
            axios({
             url: process.env.API_HOST+'/qy/showComInfo',
             method: "post",
            
             headers: {
            'Content-Type':'application/json;charset=UTF-8',
             token:JSON.parse(localStorage.getItem('data')).token
             },
              data: JSON.stringify({
                  companyCode:JSON.parse(localStorage.getItem('data')).companyCode
              })
             }).then(res=>{
                 console.log(res)
                    console.table(res.data.data)
                    this.data=res.data.data;
             })
        }
    },
}
</script>
<style >
  #comdetail .top{
   border-bottom: 1px solid #ccc;
   text-align: left;
   font-size: 24px;
   margin-bottom: 15px;
  }
   #comdetail div.cell{
    text-align: center
}
  
</style>


