<template>
    <div class="companyinfo">
       <el-row :gutter="10">
        <el-col :xs="24" :sm="12" :md="8" ><div class="grid-content ">
            <table>
                <tr>
                    <td><span style="color:red">*</span></td>
                    <td><input type="text" v-model="receiveId"></td>
                </tr>
            </table>
            </div></el-col>
        <el-col :xs="24" :sm="12" :md="8" ><div class="grid-content">
            <table>
                <tr>
                    <td><span style="color:red">*</span></td>
                    <td><input type="text" v-model="owner"></td>
                </tr>
            </table>
            </div></el-col>
        <el-col :xs="24" :sm="12" :md="8" ><div class="grid-content">
            <table>
                <tr>
                     <td ><span style="color:red;">*</span></td>
                    <td><input type="text" v-model="plateNumber"></td>
                </tr>
            </table>
            </div></el-col>
        <el-col :xs="24" :sm="12" :md="8" ><div class="grid-content ">
            <table>
                <tr>
                    <td><span style="color:red">*</span></td>
                    <td><select name="" id="" v-model="carType"><option value="" disabled></option>
                     <option :value="item.typeCode"   v-for="item of carTypelist" :key="item">{{item.typeName}}</option>
                   </select></td>
                </tr>
            </table>
            </div></el-col>
        <el-col :xs="24" :sm="12" :md="8" ><div class="grid-content ">
            <table>
                <tr>
                    <td><span style="color:red">*</span></td>
                    <td><input type="text" v-model="brand"></td>
                </tr>
            </table>
            </div></el-col>
        <el-col :xs="24" :sm="12" :md="8" ><div class="grid-content ">
            <table>
                <tr>
                    <td><span style="color:red">*</span></td>
                    <td>
                         <div style="width:188px;height:28px;border:1px solid #999;background:#FFF" id="time"> 
                                <el-date-picker
                                   v-model="createTime"
                                   type="date"
                                   placeholder=""  size="mini" style="width:150px;">
                               </el-date-picker>
                        </div> 
                    </td>
                </tr>
            </table>
            </div></el-col>
       <el-col :span="8" :offset="16" style="margin-top:15px;" :xs="24"><div class="grid-content ">
            <el-button type="primary" size="small" @click="submit"></el-button>
            <el-button type="warning" size="small" @click="reset"></el-button>
            </div></el-col>
            <el-col :md="8" class="btn">
                <el-button size="small" @click="refresh"></el-button >
                <el-button size="small"  @click="addinfo"></el-button >
                <el-button size="small" @click="updatainfo"></el-button>
               
            </el-col>
</el-row> 
  <el-table
      
       ref="singleTable"
        @current-change="handleCurrentChange"
       highlight-current-row
      :data="tableData"
      style="width: 90%;margin-top:20px;margin-left:5%;border-top: 1px solid #ccc;" size="small">
      <el-table-column
        prop="receiveId"
        label=""
        width="250"
       >
      </el-table-column>
      <el-table-column
        prop="owner"
        label=""
       >
      </el-table-column>
      <el-table-column
        prop="plateNumber"
        label="">
      </el-table-column>
      <el-table-column
        prop="carType"
        label="">
      </el-table-column>
      <el-table-column
        prop="brand"
        label="">
      </el-table-column>
      <el-table-column
        prop="color"
        label="">
      </el-table-column>
      <el-table-column
        prop="sender"
        label="">
      </el-table-column>
      <el-table-column
        prop="taker"
        label="">
      </el-table-column>
     
    </el-table>
    <div style="position:absolute;bottom:30px;left:30%;">
    <el-pagination
       @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="10"
      layout="total, prev, pager, next, jumper,slot"
      :total="total"  :small="true" :background="true" prev-text="" next-text="">
      <span style="margin:0 10px;color:#666;font-family:'Cursive';font-weight:normal">10</span><span style="margin:0 10px;color:#666;font-weight:normal">{{currentPage}}/{{pages}}</span>
    </el-pagination>
    </div>
    </div>
</template>
<script>
import axios from 'axios';
export default {
    
    created() {
      this.getdata(1)
       this.getCarType()
    },
    mounted() {
     
    },
    methods: {
      reset(){
         this.owner="",
         this.receiveId="",
         this.plateNumber="",
         this.carType="",
         this.createTime="",
         this.brand="",
         this.carTypelist=""
      },
      getCarType(){
         axios({
         url: process.env.API_HOST+'/qy/getCarType',
         method: "post",
          headers: {
          "Content-Type": "application/json;charset=UTF-8",
           token:JSON.parse(localStorage.getItem('data')).token
        }
      }).then(res=>{
        console.log(res)
        this.carTypelist=res.data.data;
      })
      },
      submit(){
       
            this.getdata(1)
            
      },
      updatainfo(){
        if(this.currentrow==""){
            this.$message({
                          message:"",
                          type:"warning"
                        })
           return
        }else{
          
          localStorage.setItem("carinfo",JSON.stringify(this.currentrow))
           this.$router.push('/updatainfo')
        }
        
 
           
          
      },
      refresh(){
        this.handleCurrentChange(1 )
      },
      addinfo(){
        this.$router.push("/carinfo")
      },
      getdata(num){
     
        axios({
            method: 'post',
                headers: {
                    'Content-Type':'application/json;charset=UTF-8',
                     token:JSON.parse(localStorage.getItem('data')).token
                        },
                         data:JSON.stringify({
                           "pageSize":"10",
	                          "pageNum":num,
                            "owner":this.owner,
                            "receiveId":this.receiveId,
                            "plateNumber":this.plateNumber,
                            "carType":this.carType,
                            "createTime":this.createTime,
                            "brand":this.brand
                           }) ,
         url:process.env.API_HOST+ '/qy/listCarBasicInfo',
        }).then(res=>{
          console.log(res)
        
          this.tableData=res.data.data.list;
          this.total=res.data.data.total;
          this.pages=res.data.data.pages;
          this.currentPage=res.data.data.currentPage;
        })
      },
         handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        // console.log(`: ${val}`);
        this.currentrow=val
       console.log(val)
        this.getdata(val)
      },
    },
     data() {
      return {
         owner:"",
        receiveId:"",
        plateNumber:"",
        carType:"",
        createTime:"",
        brand:"",
        value1: '',
        currentrow:"",
        tableData:"",
        total:'',
        pages:"",
        currentPage:"1",
        carTypelist:""
      }
     }
}
</script>
<style scope>
  .companyinfo .el-col table {
   margin: 0 auto;
  } 
  .companyinfo .el-col table tr td input{
      width: 185px;
      height: 26px;
  }
  .companyinfo .el-col table tr td select{
      width: 190px;
      height: 30px;
  }
  .companyinfo .el-col table tr td:nth-child(1){
    width: 89px;
  }
  .companyinfo .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .companyinfo .el-col table tr td #time input.el-input__inner{
    border:0  !important;
    width: 150px;
} 
.companyinfo .btn button.el-button{
    color: #22508F;
    border: 1px solid #22508F;
}
.companyinfo div.cell{
    text-align: center
}
 .companyinfo .el-table__body tr.current-row>td{
  background: #22508F;
  color: #ffff; 
}


 .companyinfo .el-table tbody tr:hover>td { 
  background: #22508F;
   color: #ffff;
}
</style>



